@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.App{
    width: 100%;
    height: 100vh;
    background: linear-gradient(360deg,#bbe7ff, #42b3f5);
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Poppins;
    padding: 2%;
}

.box{
    width: 426px;
    height: 638px;
    /* background-image: url(../public/assets/pokedex.png);
    background-size: cover;
    background-position: center; */
    color: #3a3a3a;
    display: flex;
    flex-wrap: wrap;
    position: relative;
}

.box img{
    width: 100%;
}

.nome-pokemon{
    width: 300px;
    position: absolute;
    top: 55%;
    left: 20%;
    display: flex;
    gap: 10px;    
}

.nome-pokemon div p{
    font-size: 23px;
    font-weight: bold;
    text-transform: uppercase;
}

.img-pokemon{
    width: 190px;
    height: 190px;
    position: absolute;
    top: 25%;
    left: 23%;
}

.img-pokemon img{
    width: 100%;
    height: 100%;
}

.buscar-pokemon{
    position: absolute;
    top: 65%;
    left: calc(26% - 15px);
}

.pesquisa{
    display: flex;
    box-shadow: -2px 2px 0 0 #575757, -4px 4px 0 0 #3a3a3a;
}

.pesquisa input[type='text']{
    width: 215px;
    padding: 7px;
    outline: 0;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: bold;
}

.pesquisa input[type='text']::placeholder{
    font-size: 10px;
    font-weight: bold;
}



.btns{
    margin-top: 35%;
    right: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 7px;    
}

.btns button{
    width: 90px;
    height: 32px;
    background-color: rgba(40, 40, 40, 0.85);
    color: #fff;
    font-weight: bold;
    box-shadow: -2px 2px 0 0 #757575, -3px 3px 0 0 #6d6d6d;
    border: 0;
    border-radius: 3px;
}

.btns button:active{
    font-size: 11.5px;
}

.loading{
    position: absolute;
    top: 22%;
    left: 11%;
    color: #6a6a6a;
    font-size: 16px;
    font-weight: bold;
}

@media screen and(max-width: 450px){
    .App > .box{
        width: 360px;
        height: 539.17px;
    }
}